.loader{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 2px dashed #064685;
    animation: rotate 1000ms infinite linear;
}

@keyframes rotate {
    from {

        transform: rotate(0) scale(1);
    }
    to {
        transform: rotate(360deg) scale(1.3);
    }

}